@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@200;400;500;600;700;800&display=swap");

:root {
    --teal-background: #99f6e4;
    --teal-400: #2dd4bf;
    --dark-teal: #134e4a;
    --light-blue: #304a58;
    --red: #f94144;
    --teal: #14b8a6;
    --transition: 0.5s ease-in-out;
    --fixed-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    --elevate-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
    --background: white;
    --card-color: rgb(241, 240, 240);
    --card-color2: white;
    --svg-color: black;
}

[data-theme="dark"] {
    --background: black;
    --dark-teal: #115e59;
    --light-blue: #5eead4;
    --teal-400: #12131d;
    --teal-background: #12131d;
    --card-color: #12131d;
    --card-color2: #12131d;
    --red: #12131d;
    --svg-color: #8eecf5;
    --elevate-shadow: rgb(13,148,136) 0px 25px 50px -12px;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

body {
    font-family: "Nunito", sans-serif;
}

.app {
    background-color: var(--background);
    transition: var(--transition);
}

.heading-title {
    font-size: 40px;
    margin-top: 30px;
    margin-bottom: 30px;
    color: var(--dark-teal);
    text-align: center;
    font-weight: 700;
}

html,
body,
.app {
    overflow: visible;
}

ul {
    list-style-type: none;
}

.mx-3 {
    margin: 0 1.5rem;
}

.py-3 {
    padding: 3rem 0;
}

::-webkit-scrollbar {
    background-color: white;
    width: 0.3rem;
}

::-webkit-scrollbar-thumb {
    background-color: #14b8a6;
}

@media only screen and (min-device-width: 280px) and (max-device-width: 1080px) {
    html,
    body,
    .app {
        max-width: 100vw;
        overflow-x: hidden;
    }

    .app::-webkit-scrollbar {
        display: none;
    }
}

@media only screen and (min-device-width: 1200px) {
    .mx-3 {
        margin: 0 5rem;
    }
}
